:root {
  --fs-xl: 2rem;
  --fs-600:  1.5rem;
  --fs-500: 1.25rem;
  --fs-400: 1rem;
}

@media (min-width: 40em) {
  :root {
    --fs-xl: 2rem;
    --fs-600:  1.5rem;
    --fs-500: 1.25rem;
    --fs-400: 1.125rem;
  }
}

/* text and image display */
h1, h2, h3, h4, p, a, input, textarea {
  font-family: Space Grotesk;
}

h1, h2 {
  text-align: center;
  margin: 0px 15px 10px 15px; 
}

h1 {
  font-size: var(--fs-xl);
}

h2 {
  font-size: var(--fs-600);
}

h3 {
  margin: 15px 0px 5px 0px;
  font-size: var(--fs-500);
}

p {
  font-size: var(--fs-400);
  margin:15px;
}

img {
  border: 1px solid black;
  margin: 15px;
}

/* nav */
nav {
  display: flex;
  flex-wrap: wrap;
  margin: 3px 0px 20px 0px;
  padding: 10px;
  justify-content: center;
  align-items: center;
}

.navlinks {
  border-bottom: 1px solid black;
  padding: 10px;
  justify-self: center;
}

h2.myName {
  position: absolute;
  top: 1rem;
  left: .5rem;
}

.nav-link, .foot-link {
  color: black;
  text-decoration: none;
  padding: 5px;
  margin: 5px;
}

.active {
  text-decoration-line: underline;
  text-underline-offset: 12px;
  text-decoration-thickness: 3px;
}


.nav-link:hover {
  text-decoration-line: underline;
  text-underline-offset: 12px;
  text-decoration-thickness: 3px;
  text-decoration-color: rgb(255, 230, 0)
}


 /* content */
.about, .contact, .resume {
  max-width: 700px;
  margin: 15px;
}

.portfolio {
  margin: 15px;
}

.content {
  display: flex;
  justify-content: center;
}

/* about */
.abouttext {
  max-width: 375px;
  text-align: justify;
}

.headshotdiv, .skills {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.headshot {
  max-width: 300px;
}

/* portfolio */
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.project {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.projthumb {
  max-width: 600px;
  height: auto;
}

.projlinks {
  display: flex;
  justify-content: space-around;
}

.projlink, .resumelink, .submit, textarea, input {
  border: 1px solid black;
}

.projlink, .resumelink, .skill {
  border-radius: 50px;
  padding: 5px 10px 5px 10px;
  margin: 5px;
  flex-flow: row wrap;
}

.skill {
  border: 1px solid rgb(211, 211, 211);
}

.projlink, .resumelink {
  text-decoration: none;
  color: black;
}

.resumelink {
  display: flex;
  align-items: center;
}

.projlink:hover, .resumelink:hover, .submit:hover {
  background-color: rgb(255, 230, 0);
}

/* contact */
input, textarea {
  width: 400px;
  padding: 10px;
  border-radius: 10px;
}

textarea {
  min-height: 200px;
}

small {
  color: rgb(241, 32, 0);
  display: block;
  visibility: hidden;
}

/* resume */
.resumelinkdiv {
  display: flex;
  justify-content: center;
  margin: 15px;
}

.resumetext {
  font-size: 20px;
  margin: 5px;
}

.foot-link:hover {
  text-decoration-line: underline;
  text-underline-offset: -26px;
  text-decoration-thickness: 3px;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footerlinks {
  border-top: 1px solid black;
  padding: 10px;
  position: absolute;
  bottom: 2%;

}

@media screen and (max-width: 625px) {
  .projthumb, .headshot {
    width: 300px;
  }
  textarea, input {
    width: 300px;
  }
  h2.myName {
    position: static;
  }
}